.header {
  font-size: 17px;
  &__inner {
    padding: 15px 0;
    @media (max-width: 1200px) {
      padding: 15px 10px;
    }
    @media (max-width: 991px) {
      max-width: 768px;
    }
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 43px;
    @media (max-width: 767px) {
      margin-top: 8px;
    }
    & .header__item:nth-last-child(-n + 2) {
      @media (max-width: 767px) {
        @media (max-width: 767px) {
          margin: 0 10px;
        }
      }
    }
  }
  &__logo {
    @media (max-width: 991px) {
      max-width: 160px;
    }
    @media (max-width: 767px) {
      display: none;
    }
  }
  &__link {
    cursor: pointer;
    &:visited {
      color: #222222;
    }
  }
  &__profile-text {
    @media (max-width: 991px) {
      display: none;
    }
  }
  &__profile-icon {
    height: 20px;
    width: 40px;
    @media (min-width: 991px) {
      display: none;
    }
  }
  &__nav {
    & .nav__item:not(:last-child) {
      margin-right: 36px;
      @media (max-width: 1200px) {
        margin-right: 10px;
        padding: 10px;
      }
    }
  }
  &__tel {
    @media (max-width: 991px) {
      margin-left: 20px;
    }
    & svg {
      @media (min-width: 991px) {
        display: none;
      }
    }
    & span {
      margin-left: 15px;
      font-size: 16px;
      color: transparent;
      -webkit-background-clip: text;
      background-clip: text;
      background-image: $bg-primary;
      position: relative;
      background-size: 200% 100%, auto;
      background-position: 100% 0, 0 0;
      transition: 0.5s linear;
      font-family: "PF Agora Sans Pro Regular";
      @media (max-width: 991px) {
        display: none;
      }
      &::before {
        content: "";
        width: 100%;
        height: 1px;
        background: $bg-primary;
        position: absolute;
        bottom: -2px;
      }
      &:hover {
        background-position: -100% 0, 0 0;
      }
    }
  }
  &__btn {
    display: flex;
    padding: 14px 34px;
    height: 53px;
    width: 257px;
    box-sizing: border-box;
    border-radius: 40px;
    color: $tx-light;
    font-family: "PF Agora Sans Pro Medium", sans-serif;
    font-size: 17px;
    background-image: $bg-primary;
    background-size: 100% 100%;
    transition: 0.5s linear;
    cursor: pointer;
    &:hover {
      background-size: 300% 100%;
    }
    @media (max-width: 767px) {
      display: none;
    }
  }
  &__burger {
    width: 26px;
    height: 21px;
    margin-right: auto;
  }
  &__favorites {
    background: #fff5f9;
    border-radius: 50%;
    height: 36px;
    width: 36px;
    display: block;
    line-height: 42px;
    text-align: center;
    margin-left: 53px;
    @media (max-width: 1200px) {
      margin: 0;
    }
  }
  &__icon {
    height: 36px;
    width: 36px;
  }
  &__basket {
    display: flex;
    color: $dingy;
    font-size: 17px;
    line-height: 38px;
    margin-left: 39px;
    @media (max-width: 1200px) {
      margin: 0;
    }
    & span {
      line-height: 38px;
      @media (max-width: 767px) {
        display: none;
      }
    }
  }
  &__search {
    margin-left: 60px;
    @media (max-width: 1200px) {
      margin: 0;
    }
    @media (max-width: 767px) {
      width: 100%;
    }
  }
}
