.receipt {
  padding: 100px 0 200px;
  color: $tx-primary;
  @media (max-width: 767px) {
    padding: 10px 0 20px;
  }
  &__inner {
    max-width: 619px;
    margin: 0 auto;
    box-shadow: 0px 4px 43px rgba(0, 0, 0, 0.2);
    border-radius: 11px;
    padding: 58px 44px 65px;
    box-sizing: border-box;
    @media (max-width: 767px) {
      padding: 7px;
    }
  }
  &__top {
    margin-bottom: 55px;
  }
  &__bottom {
    max-width: 503px;
  }
  &__title {
    margin-bottom: 30px;
  }
  &__post-title {
    margin-bottom: 21px;
    text-align: center;
  }
  &__order {
    margin-bottom: 22px;
  }
  &__summ {
    margin-bottom: 20px;
    font-size: 15px;
  }
}
